@import url(https://fonts.googleapis.com/css?family=Fira+Sans|Nunito|Source+Sans+Pro|Roboto|Playfair+Display:ital@0;1&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
    outline: none;
}

body {
  min-height:100%;
  position:relative;
  background-color: rgba(194, 194, 194, 0.1);
}

a {
  text-decoration: none; 
}

.button.element {
    width: 200px;
    font-family: 'Roboto', sans-serif;
    letter-spacing: 0.05em;
    font-size: 20px;
    font-weight: 600;
    color: #fff;
    cursor: pointer;
    margin-left: 10px;
    margin-top: 50px;
    height: 55px;
    text-align: center;
    text-transform: uppercase;
    border: none;


}

.element {
  background-size: 300% 100%;
  border-radius: 50px;
  moz-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
}

.element:hover {
    background-position: 100% 0;
    moz-transition: all .4s ease-in-out;
    transition: all .4s ease-in-out;
}

.element:focus {
    outline: none;
}

.element.color-1 {
    background-image: linear-gradient(to right, #25aae1, #40e495, #30dd8a, #2bb673);
    box-shadow: 0 4px 15px 0 rgba(49, 196, 190, 0.75);
}
.element.color-2 {
    background-image: linear-gradient(to right, #f5ce62, #e43603, #fa7199, #e85a19);
    box-shadow: 0 4px 15px 0 rgba(229, 66, 10, 0.75);
}
.element.color-3 {
    background-image: linear-gradient(to right, #667eea, #764ba2, #6B8DD6, #8E37D7);
    box-shadow: 0 4px 15px 0 rgba(116, 79, 168, 0.75);
}
.element.color-4 {
    background-image: linear-gradient(to right, #fc6076, #ff9a44, #ef9d43, #e75516);
    box-shadow: 0 4px 15px 0 rgba(252, 104, 110, 0.75);
}
.element.color-5 {
    background-image: linear-gradient(to right, #0ba360, #3cba92, #30dd8a, #2bb673);
    box-shadow: 0 4px 15px 0 rgba(23, 168, 108, 0.75);
}
.element.color-6 {
    background-image: linear-gradient(to right, #009245, #FCEE21, #00A8C5, #D9E021);
    box-shadow: 0 4px 15px 0 rgba(83, 176, 57, 0.75);
}
.element.color-7 {
    background-image: linear-gradient(to right, #6253e1, #852D91, #A3A1FF, #F24645);
    box-shadow: 0 4px 15px 0 rgba(126, 52, 161, 0.75);
}
.element.color-8 {
    background-image: linear-gradient(to right, #29323c, #485563, #2b5876, #4e4376);
    box-shadow: 0 4px 15px 0 rgba(45, 54, 65, 0.75);
}
.element.color-9 {
    background-image: linear-gradient(to right, #25aae1, #4481eb, #04befe, #3f86ed);
    box-shadow: 0 4px 15px 0 rgba(65, 132, 234, 0.75);
}
.element.color-10 {
        background-image: linear-gradient(to right, #ed6ea0, #ec8c69, #f7186a , #FBB03B);
    box-shadow: 0 4px 15px 0 rgba(236, 116, 149, 0.75);
}
.element.color-11 {
       background-image: linear-gradient(to right, #eb3941, #f15e64, #e14e53, #e2373f);
       box-shadow: 0 7px 15px rgba(242, 97, 103, .4);
}

.element.color-12 {
    background-image: linear-gradient(to right, #29323c, #485563, #2b5876, #4e4376);
    box-shadow: 0 4px 15px 0 rgba(45, 54, 65, 0.75);
    opacity: 0.5;
}

.home{display:flex;flex-direction:column;align-items:center}

canvas{border-radius:10px;box-shadow:0 15px 15px 0 rgba(87,87,87,0.5);background-color:#000}

.upload_sketch_container{display:flex;flex-direction:column;align-items:center}.upload_sketch_container .error-panel{text-align:center;margin:10px;padding:10px;color:rgba(0,0,0,0.77);margin-bottom:3rem}.upload_sketch_container .error-panel .false{color:rgba(222,1,1,0.77)}.upload_sketch_container .error-panel .true{color:rgba(36,156,0,0.77)}.upload_sketch_container .input_container{margin-top:50px}.upload_sketch_container .input_container [type="file"]{height:0;overflow:hidden;width:0}.upload_sketch_container .input_container [type="file"]+label{background-image:linear-gradient(to right, #29323c, #485563, #2b5876, #4e4376);box-shadow:0 4px 15px 0 rgba(45,54,65,0.75);border:none;border-radius:5px;color:#fff;cursor:pointer;display:inline-block;font-family:'Nunito', sans-serif;font-size:inherit;font-weight:600;text-transform:uppercase;outline:none;padding:1rem 50px;position:relative;transition:all 0.3s;vertical-align:middle;background-size:300% 100%;border-radius:50px;moz-transition:all .4s ease-in-out;transition:all .4s ease-in-out}.upload_sketch_container .input_container [type="file"]+label:hover{background-position:100% 0;moz-transition:all .4s ease-in-out;transition:all .4s ease-in-out;box-shadow:none}.upload_sketch_container .image_sketch_container{display:grid;grid-template-columns:1fr 1fr;align-items:center;grid-column-gap:5em;margin-bottom:5em}.upload_sketch_container .image_sketch_container .image_container{border:1px solid rgba(148,142,148,0.22);border-radius:10px;box-shadow:0 15px 15px 0 rgba(87,87,87,0.26);width:500px;height:500px}.upload_sketch_container .image_sketch_container .image_container img{padding:40px;max-width:500px;max-height:500px;position:relative;top:50%;left:50%;transform:translate(-50%, -50%)}@media only screen and (max-width: 1200px){.upload_sketch_container .image_sketch_container{grid-template-columns:1fr 1fr;align-items:center;grid-column-gap:5em}.upload_sketch_container .image_sketch_container .image_container{width:350px;height:350px}.upload_sketch_container .image_sketch_container .image_container img{max-width:350px;max-height:350px}}@media only screen and (max-width: 840px){.upload_sketch_container .image_sketch_container{grid-template-columns:1fr;align-items:center;grid-row-gap:5em}.upload_sketch_container .image_sketch_container .image_container{width:350px;height:350px}.upload_sketch_container .image_sketch_container .image_container img{max-width:350px;max-height:350px}}

