s:1fr 2fr;align-items:center;width:700px;height:700px}.header .image_description_container .fourier_image img{max-width:250px;max-height:600px;top:5%;left:50%;transform:translateX(-50%);margin:0}.header .image_description_container .description{text-align:center}.header .image_description_container .description .title{font-size:2em;margin-bottom:0.5em}.header .image_description_container .description .details p{font-size:1em;line-height:1.1em;padding:0 25px 10px 25px}}@media only screen and (max-width: 700px){.header{margin:0em 0 5em 0}.header .image_description_container{width:100%;height:100%}.header .image_description_container .description{text-align:center}.header .image_description_container .description .title{font-size:2em;margin-bottom:0.5em}.header .image_description_container .description .details p{font-size:1em;line-height:1.1em;padding:0 25px 10px 25px}}@media only screen and (max-width: 420px){.header .image_description_container{width:100%;height:100%}.header .image_description_container .description{text-align:center}.header .image_description_container .description .title{font-size:2em;margin-bottom:0.2em}.header .image_description_container .description .details p{font-size:1em;line-height:1.1em;padding:0 25px 10px 25px}}

.footer{margin:5em 0 5em 0}.footer .dedication_profils_container{width:1200px;background-color:#fff;box-shadow:0 15px 25px 0 rgba(87,87,87,0.17);border-radius:10px;display:flex;flex-direction:column;align-items:center;flex-wrap:wrap}.footer .dedication_profils_container .dedication{margin:1em 0 3em 0;padding:5px;text-align:center;font-family:'Playfair Display', sans-serif;font-style:normal;font-weight:normal;font-size:1em}.footer .dedication_profils_container .profils_container{width:100%;display:flex;flex-direction:row;justify-content:space-around;flex-wrap:wrap;margin:1em 0 3em 0}.footer .dedication_profils_container .profils_container .profil{text-align:center;font-family:'Roboto', sans-serif;font-style:normal;color:rgba(0,0,0,0.77);font-size:1.5em;border-radius:10px;border:0.5px solid rgba(0,0,0,0.1);width:300px;padding:30px;margin:1em;cursor:pointer}.footer .dedication_profils_container .profils_container .profil img{border-radius:100%;border:none;background-color:#000;width:130px;margin-bottom:2em}.footer .dedication_profils_container .profils_container .profil:hover{border:0.5px solid rgba(0,0,0,0.54)}.footer .dedication_profils_container .copyrigth{margin-bottom:10px;text-align:center;font-family:'Roboto', sans-serif;font-style:normal;color:rgba(0,0,0,0.77);font-size:0.7em}@media only screen and (max-width: 1200px){.footer .dedication_profils_container{width:1100px}}@media only screen and (max-width: 1100px){.footer .dedication_profils_container{width:1000px}}@media only screen and (max-width: 1000px){.footer .dedication_profils_container{width:900px}}@media only screen and (max-width: 900px){.footer .dedication_profils_container{width:700px}}@media only screen and (max-width: 700px){.footer{margin:0em 0 0em 0}.footer .dedication_profils_container{width:100%;height:100%}}@media only screen and (max-width: 420px){.footer .dedication_profils_container{width:100%;height:100%}}

.navbar{position:-webkit-sticky;position:sticky;top:0;z-index:1;background-color:rgba(255,255,255,0.51);display:flex;flex-direction:row;justify-content:space-between;flex-wrap:wrap}.navbar ul{list-style:none;display:flex;flex-direction:row;justify-content:flex-end;flex-wrap:wrap}.navbar li{display:inline-block;padding:0.5em 1em}.navbar a{display:block;text-decoration:none;font-family:'Nunito', sans-serif;font-style:normal;font-weight:normal;font-size:13px;line-height:28px;text-align:center;letter-spacing:0.075em;color:rgba(0,0,0,0.55)}.navbar a:hover{color:black}.navbar .leftside a{font-family:'Nunito', sans-serif;font-weight:bold;font-size:20px}.navbar .selected{color:black;border-bottom:solid  2px}@media only screen and (max-width: 720px){.navbar li{padding:0.5em 1em}.navbar a{font-size:12px}}

